// e.g.  test.jpg, 
// - H5      : images/test.jpg        imageshtml
// - Android : local:///test          localdpi,hdpi
// - iOS     : local///test.jpg       local test.jpg
const getImg = function (imgName) { // 
  let platform = weex.config.env.platform
  let imgPath = ''
  if (platform === 'Web') {
    imgPath = `images/${imgName}`
  } else if (platform === 'android') { // android 
    imgName = imgName.substr(0, imgName.lastIndexOf('.'))
    imgPath = `local:///${imgName}`
  } else {
    imgPath = `local:///${imgName}` // imgPath = `../images/${imgName}`
  }
  return imgPath
}

export default getImg



